.sort-select {
  box-sizing: content-box;
    display: flex;
    padding: 8px 16px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    width: 122px;
    border-radius: 32px;
    border: 2px solid #192599;

  &__text {
    color: #192599;
    font-size: 16px;
    font-weight: 600;
    line-height: 1.4;
  }
  &__svg>use{
    stroke: #192599;
  }
}
